import React from 'react';
import TimeAgo from 'timeago-react';
import { Link } from 'react-router-dom';
function ch1(props) {
    return (
        <div>
            <div className='conCh'>
                <div className='sampulCh vol2'></div>
                <div>
                    <p><TimeAgo className='timeago'
                        datetime={'2022-11-11 18:020:08'}
                        locale='id_ID'
                    /></p>
                    <Link className='chapterLink' to="tagahumika/chapter/11"><h3>Chapter 11</h3>
                    <div className='group'>Tagahumika</div></Link>
                </div>
            </div>
        </div>
    );
}

export default ch1;