import './App.css';
import Home from './home';
import Web from './list-manga/Web';
import Serial from './list-manga/Serial';
import Nav from './komponen/nav';
import Tagahumika from './Manga/Tagahumika/Tagahumika';
import TgmkChapter from './Manga/Tagahumika/komponen/chapter';
import TgmkKomen from './Manga/Tagahumika/komponen/komen';
import TgmkTerkait from './Manga/Tagahumika/komponen/terkait';
import { HashRouter, Route, Routes } from 'react-router-dom';

function App() {
  return (
    <HashRouter>
        <Nav />
      <Routes>
        <Route path='/' element={<Home />}>
          <Route index element={<Serial />} />
          <Route index path='serial' element={<Serial />} />
          <Route path='Web' element={<Web />} />
        </Route>
        <Route path='/tagahumika' element={<Tagahumika />}>
          <Route index element={<TgmkChapter />}/>
          <Route path='komen' element={<TgmkKomen />}/>
          <Route path='terkait' element={<TgmkTerkait />}/>
        </Route>
      </Routes>
    </HashRouter>
  );
}

export default App;
