import "./page.css";
import Disqus from "disqus-react";

export default function komen() {
   const disqusShortname = "Tagahumika"
    const disqusConfig = {
      url: "https://rzitranslation.dee.al/#/tagahumika",
      identifier: "tagahumika-home",
      title: "Riztranslation - Tagahumika"
    }
   return (
      <div className="article-container">

        <h1>Page Title</h1>

        <p>Page content.</p>

        <Disqus.DiscussionEmbed
          shortname={disqusShortname}
          config={disqusConfig}
        />
      </div>
   );
}