import { Outlet, Link } from "react-router-dom";
import './tagahumika.css';
import { useLocation } from "react-router-dom";

function Main() {
const location = useLocation();
const {pathname} = location;
const splitlocation = pathname.split('/');

  return (
    <div className="main">
      <div className="sampul">
        <img src="https://pbs.twimg.com/media/FaHhTVTUUAE-lFa?format=webp" alt="sampul tagahumika" />
      </div>
      <div className="detail">
        <h3>Tagahumika</h3>
        <div style={{
            fontSize: '0.7em',
            paddingLeft: '3px',
            fontStyle: 'italic',
            transform: 'translateY(-30%)',
            fontFamily: 'poppins, sans-serief'
          }}>Tagafumika, </div>
        <h5>Hamita, Hamita</h5>
        <section>
          <a href="/">Romance</a>
          <a href="/">Drama</a>
          <a href="/">Web Comic</a>
          <a href="/">Slice of Life</a>
        </section>
          <p className="status">Status: Ongoing</p>
        <div className="deskripsi">
          <p>Wakui menjalani kehidupan kampus yang membosankan. Satu-satunya kesenangannya adalah korespondensi.
            Berawal dari sebuah surat yang ia temukan di perpustakaan universitas dan menjadi pusat kehidupan
            Wakui. Namun, suatu hari, Wakui menemukan sebuah surat yang berisi sesuatu yang tidak seharusnya
            diketahui oleh korespondennya. Wakui mencurigai bahwa koresponden mungkin adalah kenalannya, dan
            mulai mencurigai tiga gadis yang terkait dengannya. Namun, ketiga gadis itu agak eksentrik..</p>
        </div>
      </div>
      <div className="chapter" id="chapter">
        <div className="headertgmk"></div>
        <ul className="conNav">
          <li className={splitlocation[2] === "Komen" ? "" : splitlocation[2]=== "Terkait" ? "" : "on"}>
            <Link className="link" to='/tagahumika/'>Chapter</Link>
          </li>
          <li className={splitlocation[2] === "Komen" ? "on" : ""}>
            <Link className="link" to='/tagahumika/Komen'>Komen</Link>
          </li>
          <li className={splitlocation[2] === "Terkait" ? "on" : ""}>
            <Link className="link" to='/tagahumika/Terkait'>Terkait</Link>
          </li>
        </ul>
        <div style={{ margin: '10px' }}><Outlet /></div>
        <div className="borderbtm" id="borderbtm"></div>
      </div>
    </div>
  );
}

export default Main;
